import '../../static/common.css'
export default {
	data() {
		return {
			imgList: [],
			modalName: null,
			textareaAValue: '',
			labelInfo: [],
			TabCur: 0,
			picUrl: '',
			nav: [{
					name: '乡村美食'
				},
				{
					name: '民俗文化'
				},
				{
					name: '古建筑'
				},
				{
					name: '人物传记'
				}
			],
			pic: [{
					'name': '相册',
					'id': '1'
				},
				{
					'name': '拍照',
					'id': '2'
				},
			],
		};
	},
	methods: {
		labelInit() {
			let that = this
			that.$request.httpTokenRequest(that.$api.getLabels, that.$api.get, {
				Key: that.$api.Key,
				Client: that.$api.CLIENT,
				Version: that.$api.VERSION,
				labelType: 5
			}).then((res) => {
				if (res.data.flag === 200) {
					that.labelInfo = res.data.data
					console.log(that.labelInfo)
					that.TabCur = that.labelInfo[0].label_id
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					});
				}
			});
		},
		picSumbit() {
			if (this.imgList.length == 0) {
				uni.showToast({
					title: '请添加照片',
					icon: 'none'
				});
			} else if (this.textareaAValue == "") {
				uni.showToast({
					title: '请添加照片描述',
					icon: 'none'
				});
			} else {
				let that = this
				that.$request.httpTokenRequest(that.$api.addCharacteristicImg, that.$api.get, {
					Key: that.$api.Key,
					Client: that.$api.CLIENT,
					Version: that.$api.VERSION,
					villageId: that.villageId,
					villageImg: that.picUrl,
					imgDes: that.textareaAValue,
					labelId: that.TabCur
				}).then((res) => {
					if (res.data.flag === 200) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
						uni.navigateBack()

					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
					}
				});
			}

		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		RadioChange(e) {
			this.radio = e.detail.value
		},
		selectPic(id) {
			// id=1为相册  id=2 拍照 自己定义的
			if (id == 1) {
				this.hideModal()
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
							this.upload(this.imgList[0])
						} else {
							this.imgList = res.tempFilePaths
							this.upload(this.imgList[0])
						}

					}
				});
			} else if (id == 2) {
				let that = this;
				this.hideModal()
				uni.chooseImage({
					count: 1, //默认9
					sourceType: ['camera'], //拍照会掉拍照，然后存储在相册中
					success: function(res) {
						var tempFilePaths = res.tempFilePaths;
						uni.saveFile({
							tempFilePath: tempFilePaths[0],
							success: function(res) {
								var savedFilePath = res.savedFilePath;
								console.log(savedFilePath)
								if (that.imgList.length != 0) {
									that.imgList.push(savedFilePath)
									that.upload(that.imgList[0])
								} else {
									that.imgList.push(savedFilePath)
									that.upload(that.imgList[0])
								}
							}
						});
					},

				});
			}

		},
		//3.图片处理成二进制格式
		upload(url) {
			let that = this
			uni.uploadFile({
				url: that.$env.baseUrl + that.$api.fileUpload + '?type=' + 'grouptour', //后台图片上传接口
				filePath: url,
				name: "file",
				success: (uploadFileRes) => {
					let dataUrl = JSON.parse(uploadFileRes.data)
					that.picUrl = dataUrl.data[0]
				}
			});
		},

		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				// title: '召唤师',
				content: '确定要删除这张照片吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},

		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		tabSelect(id) {
			this.TabCur = id;
		}
	},
	onLoad(option) {
		this.userInfo=uni.getStorageSync('userInfo');
		this.$api.Key=this.userInfo.key
		this.villageId = option.villageId;
		this.labelInit()
	}
}
